import React, { Component } from 'react';
import './Camera.css';
import CameraPhoto, { FACING_MODES, IMAGE_TYPES } from 'jslib-html5-camera-photo';
import SwitchIcon from './../../assets/switch-camera.png';
import BackIcon from './../../assets/back-arrow.png'

class Camera extends Component {
    constructor(props, context) {
        super(props, context);
        this.cameraPhoto = null;
        this.videoRef = React.createRef();
        this.canvasRef = React.createRef();
        this.state = {
            isPhotoCaptured: false,
            generatedImg: '',
            facingMode: FACING_MODES.USER
        }
    }

    componentDidMount() {
        this.cameraPhoto = new CameraPhoto(this.videoRef.current);
        this.startCameraMaxResolution(this.state.facingMode);
    }

    async startCamera(idealFacingMode, idealResolution) {
        await this.cameraPhoto.startCamera(idealFacingMode, idealResolution)
            .then(() => {
                console.log('camera is started !');
            })
            .catch((error) => {
                console.error('Camera not started!', error);
            });
    }

    async startCameraMaxResolution(idealFacingMode) {
        await this.cameraPhoto.startCameraMaxResolution(idealFacingMode)
            .then(() => {
                console.log('camera is started !');
            })
            .catch((error) => {
                console.error('Camera not started!', error);
            });
    }

    takePhoto = () => {
        const config = {
            sizeFactor: 1
        };

        this.setState({ generatedImg: this.cameraPhoto.getDataUri(config), isPhotoCaptured: true });

        this.stopCamera();
    }

    retake = () => {
        this.setState({ generatedImg: "", isPhotoCaptured: false });
        this.cameraPhoto = new CameraPhoto(this.videoRef.current);
        this.startCameraMaxResolution(this.state.facingMode);
    }

    async stopCamera() {
        await this.cameraPhoto.stopCamera()
            .then(() => {
                console.log('Camera stoped!');
            })
            .catch((error) => {
                console.log('No camera to stop!:', error);
            });
    }

    switchCamera = () => {
        if (this.state.facingMode === FACING_MODES.USER) {
            this.setState({ facingMode: FACING_MODES.ENVIRONMENT });
            this.startCameraMaxResolution(this.state.facingMode);
        } else {
            this.setState({ facingMode: FACING_MODES.USER });
            this.startCameraMaxResolution(this.state.facingMode);
        }
    }

    render() {
        const { closeCamera, filterImg } = this.props;
        const { generatedImg, isPhotoCaptured } = this.state;
        return (
            <div>
                <div className="camera-container">
                    <div>

                        <video
                            ref={this.videoRef} autoPlay={true}></video>
                        {isPhotoCaptured && <>
                            <img src={generatedImg} alt="Generated Selfie" />
                        </>}
                        <img src={filterImg} alt="Filter Overlay" />

                        <canvas style={{ display: "none" }} ref={this.canvasRef}></canvas>

                    </div>
                    <div className="capture-controls">
                        {!isPhotoCaptured &&
                            <div>
                                <div className="capture-btn-container">
                                    <button className="capture-btn" onClick={this.takePhoto}></button>
                                </div>

                                <img src={SwitchIcon} className="switch-camera-btn" onClick={this.switchCamera} />
                                {!isPhotoCaptured && 
                                <img src={BackIcon} className="go-back-btn" onClick={() => {
                                    this.stopCamera();
                                    closeCamera();
                                }} />}
                            </div>}
                        {isPhotoCaptured &&
                            <div className="retake-btn-container">
                                <button className="retake-btn" onClick={this.retake} >Take Another</button>
                                <button className="retake-btn" onClick={this.takePhoto} >Save Picture</button>
                            </div>}
                    </div>
                </div>
            </div>
        );
    }
}

export default Camera;